.topStory-slide-container [data-swipeable] {
  overflow: hidden !important; }

.topStory-slide {
  position: relative;
  min-height: 200px;
  width: 520px;
  height: 100%;
  margin: 0 auto; }
  .topStory-slide-link {
    display: block;
    width: 100%;
    height: 100%; }
  .topStory-slide-title {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: rgba(0, 0, 0, 0.6);
    margin: 0;
    padding: 5px;
    color: #eee;
    text-align: justify;
    font-size: 14px; }



